

.contacts {


	@media (min-width: 700px) {
		display: block;
		width: 50%;
		padding-top: 150px;
		margin-top: -16px;
		margin-right: 50px;
	}
}

.contacts__title {
	font-family: "Arial";
	text-align: center;
	color: black;
	margin-top: 0;

	@media (min-width: 700px) {
		margin-bottom: 50px;
	}
}

.contacts__item {
	display: flex;
	flex-direction: column;
	margin-bottom: 25px;

	@media (min-width: 700px) {
		flex-direction: row;
	}
}

.contacts__text {
	font-family: "Arial";
	color: black;
	margin: 0;

	@media (min-width: 700px) {
	}
}

.contacts__name {
	font-family: "Arial";
	text-transform: uppercase;
	font-weight: bold;
	margin-bottom: 10px;

	@media (min-width: 700px) {
		margin-right: 100px;
		padding-left: 25px;
	}
}

.contacts__link {
	width: 150px;
	font-family: "arial";
	text-decoration: none;
	color: black;

	@media (min-width: 700px) {
	}

	&:hover {
		color: $hover;

	}
}

.contacts__list {
	@media (max-width: 700px) {
		display: flex;
		width: 100%;
		padding-left: 0;
		justify-content: space-around;
		margin-bottom: 0;
	}

	@media (min-width: 700px) {
		display: flex;
		width: 30%;
		padding-left: 0;
		margin-left: auto;
		margin-top: 150px;
		justify-content: space-between;
	}
}
.contacts__item {
}
.contacts__icon {
}

.contacts__href {
	&:hover {
		fill: $hover;
	}
}

.contacts__license {
	text-decoration: none;
	text-align: right;
	color: black;
	margin: 0;

	&:hover {
		color: $hover;
	} 
}