.button {
	@media (max-width: 700px) {
		margin-top: auto;
	}

	@media (min-width: 700px) {
		width: 35%;
		height: 50px;
		align-self: center;

		border-radius: 25px;
		font-family: "Arial";
		background-color: white;
		color: black;
		text-align: center;
		font-size: 19px;
		border: 1px solid black;
		cursor: pointer;
		margin-bottom: 15px;

		&:hover {
			box-shadow: 0px 6px 26px 0px rgba(0,0,0,0.75);
		}

		&:active {
			box-shadow: 0px 6px 26px 0px $hover;
		}
	}
}

.button-3__white {
	@media (min-width: 700px) {
		&:hover {
			box-shadow: 0px 6px 26px 0px rgba(193, 194, 196, 0.5);
		}
	}
}

.button__closed-1,
.button__closed-2,
.button__closed-3,
.button__closed-4 {
	@media (min-width: 700px) {
		align-self: flex-end;
	}
}